import React from 'react';
 import { Container,Row,Col } from 'react-bootstrap';
 import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';
 import CarruselMain from '../../../Layout/carrusel/CarruselMain';
 import CarruselFirst from '../../../Layout/carrusel/CarruselFirst';
 import CarruselSecond from '../../../Layout/carrusel/CarruselSecond';
 import CarruselThird from '../../../Layout/carrusel/CarruselThird';
 import DataTable from '../../../Components/Table';
 import LocalBranch from '../../../Location/location';

function Indexhome() {
 
   return(
     <Container  className="p-md-2 nuevocontainer">
  {/* Stack the columns on mobile by making one full-width and the other half-width */}
  <Row>
    <Col className=" border-top border-bottom border-white text-white text-uppercase text-xl-center justify-content-center align-items-xl-center"  ><h2 className="font-weight-bold">Es un gusto servite </h2></Col>
  </Row>
  <Row  className="p-md-2" >
    <Col className="justify-content-center col-xl-12 mr-2 p-md-2">
     <CarruselMain />
    </Col>
  </Row>
  {/* Columns start at 50% wide on mobile and bump up to 33.3% wide on desktop */}
  <Row >
    <Col xs={6} md={4}   className="p-md-2">
    <CarruselFirst />
    </Col>
    <Col xs={6} md={4}  className="p-md-2">
    <CarruselSecond />
    </Col>
    <Col xs={6} md={4}   className="p-md-2">
    <CarruselThird />
    </Col>
  </Row>

  {/* Columns are always 50% wide, on mobile and desktop */}
  <Row>
    <Col xs={6}   className="p-md-2 nuevocontainer">
    <LocalBranch/>
    </Col >
    <Col xs={6}   className="p-md-2 nuevocontainer" >
      <DataTable />
    </Col>
  </Row>
</Container>
   );

}

export default Indexhome;
