import React from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Indexhome from '../../Layout/Secctions/Home/indexhome';
import InitWelcome from '../../Layout/Secctions/welcom/initwelcome';
import WeUs from '../../Layout/Secctions/Weus/weus';
import Contact from '../../Layout/Secctions/Contact/contacs';

function Routing(){

        return(<>
                <Route path="/forstores" exact component={InitWelcome} /> 
                <Route path="/forstores/home" exact component={Indexhome} />
                <Route path="/forstores/contact" component={Contact} />
                <Route path="/forstores/location" component={Indexhome} />
                <Route path="/forstores/weus" component={WeUs} />
                <Route path="/forstores/promos" component={Indexhome} />
                <Route path="/forstores/destacados" component={Indexhome} />
                <Route path="/forstores/new" component={Indexhome} />
                <Route path="/forstores/clothes" component={Indexhome} />
                <Route path="/forstores/items" component={Indexhome} />
                <Route path="/things" component={Indexhome} /> 
            
       </>
        );
};
export default Routing;
