import React from 'react'
import {Form,Button,Container,Row,Col} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-grid.css';

 function Contact(){

 
return(
<Container className=" nuevocontainer">
<Row>
    <Col className=" border-top border-bottom border-white text-white text-uppercase text-xl-cente "  ><h2 className="font-weight-bold">Contactanos</h2></Col>
  </Row>
  <Row>
    <Col className="p-md-2 m-md-2 ">
    <img
      className="d-block w-100"
      src="https://imgreposwx.files.wordpress.com/2020/09/bannerstores3.jpg"
      alt="First slide"
      height="300px"
    />
    </Col>
    <Col  >
<Form id="contact-form" onSubmit='#' method="POST">
    <div className="form-group">
        <label htmlFor="name">Name</label>
        <input type="text" className="form-control" />
    </div>
    <div className="form-group">
        <label htmlFor="exampleInputEmail1">Email address</label>
        <input type="email" className="form-control" aria-describedby="emailHelp" />
    </div>
    <div className="form-group">
        <label htmlFor="message">Message</label>
        <textarea className="form-control" rows="5"></textarea>
    </div>
    <Button type="submit" className="btn btn-primary">Submit</Button>
</Form>
    </Col>
  </Row>
  </Container>

)
}
export default Contact;